import React from 'react';
import Person from './Person';

const Numbers = ({persons, setPersons, messager}) => {
    //Saaduista henkilöistä muodostetaan html-koodia Person-komponentin ja map-funktion avulla
    const contacts = () => persons.map(person => 
                //console.log("Uuden henkilön luonti on aloitettu") 
                <Person 
                key={person.name} 
                person={person} 
                group={persons} 
                setGroup={setPersons}
                messager={messager}/>
            )
    return(
        <div>
            <p>{contacts()}</p>
            <p>Täällä tapahtuu kummallisuuksia</p>
        </div>
    )
}
export default Numbers